@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@500;600;700&display=swap");
* {
  box-sizing: border-box;
  cursor: default;
  margin: 0;
  padding: 0;
  font-family: "League Spartan";
  outline: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  caret-color: transparent;
}

html {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 62.5%;
  background-color: hsl(0, 0%, 100%);
}

body {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  background-attachment: fixed;
  color: rgb(33, 33, 33);
}

.link, a {
  text-decoration: none;
  color: rgb(33, 33, 33);
  cursor: pointer;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

button {
  cursor: pointer;
}

.page-section-title {
  font-size: 2.4rem;
  margin: 3rem 0 3rem 0;
}

.visible {
  opacity: 1;
}

h2 {
  font-size: 2rem;
}

.round {
  border-radius: 50%;
}

.page-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: space-between;
  gap: 0;
  background-color: white;
  min-height: 100vh;
}
.page-container .top-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  gap: 0;
  width: 100%;
  height: auto;
  height: 65%;
  position: relative;
}
.page-container .top-container .top-left-container {
  flex: 1;
  width: 100%;
  height: 100%;
  background-color: red;
}
.page-container .top-container .top-left-container .top-left-image {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.page-container .top-container .btn-container {
  width: 12%;
  height: 8rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 0;
  background-color: hsl(0, 0%, 0%);
  position: absolute;
  top: 100%;
  right: 30%;
  transform: translateY(-100%);
}
.page-container .top-container .btn-container .btn {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  cursor: pointer;
  transition: all 0.2s ease;
}
.page-container .top-container .btn-container .btn:hover {
  background-color: hsl(0, 0%, 27%);
}
.page-container .top-container .btn-container .btn img {
  width: 2rem;
  height: auto;
}
.page-container .top-container .top-right-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 0;
  width: 42%;
  height: 100%;
  background-color: white;
  padding: 6rem 2rem 0 2rem;
}
.page-container .top-container .top-right-container .text-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1.8rem;
  max-width: 45rem;
}
.page-container .top-container .top-right-container .text-container .page-title {
  color: hsl(0, 0%, 0%);
  font-family: "League Spartan";
  font-weight: 700;
  font-size: 4.2rem;
  font-style: normal;
}
.page-container .top-container .top-right-container .text-container .page-description {
  color: hsl(0, 0%, 63%);
  font-family: "League Spartan";
  font-weight: 500;
  font-size: 1.6rem;
  font-style: normal;
  line-height: 2.5rem;
}
.page-container .top-container .top-right-container .text-container .shop-link {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
  text-transform: uppercase;
  color: hsl(0, 0%, 0%);
  font-family: "League Spartan";
  font-weight: 600;
  font-size: 1.6rem;
  font-style: normal;
  letter-spacing: 1.4rem;
}
.page-container .top-container .top-right-container .text-container .shop-link p {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  cursor: pointer;
  transition: color 0.2s linear;
}
.page-container .top-container .top-right-container .text-container .shop-link p:hover {
  color: hsl(0, 0%, 63%);
}
.page-container .bottom-container {
  width: 100%;
  height: 35%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  gap: 0;
}
.page-container .bottom-container .bottom-left-container {
  width: 30%;
  height: 100%;
  overflow: hidden;
}
.page-container .bottom-container .bottom-left-container .bottom-left-image {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
}
.page-container .bottom-container .bottom-right-container {
  overflow: hidden;
  width: 30%;
  height: 100%;
  position: relative;
  top: 0;
  right: 0;
  background-color: green;
}
.page-container .bottom-container .bottom-right-container .bottom-right-image {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
}
.page-container .bottom-container .bottom-center-container {
  height: 100%;
  width: 40%;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  padding: 5rem;
}
.page-container .bottom-container .bottom-center-container .bottom-text-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1.6rem;
}
.page-container .bottom-container .bottom-center-container .bottom-text-container .bottom-text-title {
  color: hsl(0, 0%, 0%);
  font-family: "League Spartan";
  font-weight: 700;
  font-size: 1.8rem;
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 0.6rem;
}
.page-container .bottom-container .bottom-center-container .bottom-text-container .bottom-text-description {
  color: hsl(0, 0%, 63%);
  font-family: "League Spartan";
  font-weight: 500;
  font-size: 1.6rem;
  font-style: normal;
  line-height: 2.2rem;
}

@media only screen and (min-width: 650px) {
  .top-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: space-between;
    gap: 0;
  }
}
.navbar-container {
  width: 100%;
  height: 7rem;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 5rem;
  z-index: 10;
  background-color: transparent;
  padding: 5.5rem 0 2rem 7rem;
}
.navbar-container .navbar-title {
  color: hsl(0, 0%, 100%);
  font-family: "League Spartan";
  font-weight: 500;
  font-size: 3.4rem;
  font-style: normal;
}
.navbar-container .links-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 4rem;
}
.navbar-container .links-container .navbar-link {
  color: hsl(0, 0%, 100%);
  font-family: "League Spartan";
  font-weight: 500;
  font-size: 2rem;
  font-style: normal;
  cursor: pointer;
  position: relative;
}
.navbar-container .links-container .navbar-link .selector {
  width: 80%;
  height: 0.3rem;
  background-color: white;
  position: absolute;
  top: 130%;
  right: 0;
  transition: all 0.2s linear;
  opacity: 0;
}
.navbar-container .links-container .navbar-link .selector.selected {
  opacity: 1;
}/*# sourceMappingURL=main.css.map */